@use '../utils/var';
@use '../utils/function' as fn;

.sc2{
    position: relative;
    nav{
        opacity: 0;
        display: none;
        align-items: center;
        justify-content: center;
        padding-top: 100px;
        z-index: 1;
        position: relative;
        gap: 10px;
        button{
            height: 60px;
            padding: 0 40px;
            border-radius: 30px;
            background-color: transparent;
            transition: all 0.25s;
            &[aria-selected="true"]{
                background-color: #fff;
                span{
                    color: #000;
                }
            }
            span{
                font-weight: 600;
                color: #fff;
                font-size: 25px;
            }
        }
    }
    .switch-panel-track{
        height: 100vh;
        width: 100%;
    }
    .switch-panel{
        height: 100%;
        width: 100%;
        display: flex;
    }
    .picture{
        width: 50%;
        height: 100%;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    .details{
        .hgroup{
            h3{
                color: #fff;
                font-size: 60px;
                @media (max-width: 1440px) {
                    font-size: 40px;
                }
                @media (max-width: 1024px) {
                    font-size: 30px;
                }
            }
            p{
                color: #fff;
                font-size: 32px;
                padding-top: 22px;
                @media (max-width: 1440px) {
                    font-size: 24px;
                }
                @media (max-width: 1024px) {
                    font-size: 16px;
                }
            }
        }
        .link-container{
            padding-top: 12.5vh;
            display: flex;
            gap:0 95px;
            @include fn.responsive(lg){
                padding-top: 4rem;
                flex-direction: column;
                gap: 4rem 0;
            }
            a{
                display: block;
                strong{
                    color: #fff;
                    transition: all 0.25s;
                    font-weight: 600;
                    font-size: 25px;
                    display: inline-flex;
                    align-items: center;
                    gap:0 10px;
                    &::after{
                        content:"";
                        display: inline-block;
                        width: 17px;
                        height: 17px;
                        background: url(../images/main/sc2-arw.png) no-repeat 50% 50%;
                        transition: all 0.15s;
                    }
                    @include fn.responsive(md){
                        font-size: 14px;
                        &::after{
                            width: 12px;height: 12px;
                            background-size: 100% auto;
                        }
                    }
                }
                div{
                    color: #717171;
                    font-size: 16px;
                    padding-top: 20px;
                    line-height: 1.34;
                    transition: all 0.25s;
                    @include fn.responsive(md){
                        font-size: 12px;
                        
                    }
                }
                &:hover,
                &:focus{
                    strong{
                        color: #81d2e5;
                        &::after{
                            background-image: url(../images/main/s2-arw-active.png);
                        }
                    }
                    div{
                        text-decoration: underline;
                        color: #fff;
                    }
                }
            }
        }
    }
}

