        opacity: 1;
        transition: opacity 0.5s 0.3s;
    }
}
.sc2{
    .slide-dot-wrap{padding-top: 20px;}
    .slick-dots li button{
        width: 8px;height: 8px;
        border: 0;
        background-color: #fff;
        opacity: 0.2;
    }
    .slick-dots li.slick-active button, .slick-dots li.slick-stop button{
        width: 8px;height: 8px;
        border: 0;
        opacity: 1;
        background-color: #fff;
        &::before{display: none;}
    }
    
}