  }
}
#breadcrumb--container > li > a {
  position: relative;
  height: 100%;
  line-height: 6rem;
  color: #000;
  font-size: 1.7rem;
  display: block;
}
@media (max-width: 768px) {
  #breadcrumb--container > li > a {
    line-height: 4rem;
    font-size: 1.3rem;
  }
