.form-radio input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  opacity: 0.1;
}
.form-radio i {
  display: inline-block;
  position: relative;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  border: 1px solid #ddd;
  background: #fff;
  overflow: hidden;
  transition: all 0.25s;
}
@media (max-width: 1024px) {
  .form-radio i {
    width: 1.6rem;
    height: 1.6rem;
  }
}
.form-radio i::after {
  content: "";
  width: 0;
  height: 0;
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.25s;
  border-radius: 50%;
  background-color: #fff;
}
.form-radio span {
  color: #555555;
  font-size: 1.5rem;
}
@media (max-width: 1170px) {
  .form-radio span {
