/* switch */
.sc-switch {
  display: none;
  height: 0 !important;
  width: 0 !important;
  visibility: hidden;
}

.sc-switch-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 38px;
  height: 24px;
  background: #e0e0e0;
  display: block;
  border-radius: 100px;
  position: relative;
  transition: all ease 0.8s;
}

.sc-switch-label:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s;
}

input:checked + label {
  background: #0071bb;
}

input:checked + label:after {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}

.sc-switch-label:active:after {
  width: 30px;
}

