.toggle-ti-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 10px 0;
}

.toggle-ti {
  border: 1px solid #c6c6c6;
  border-radius: 4px;
  background: #fff;
  min-width: 130px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  padding: 5px 10px;
}
